body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.app {
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f0f0f0;
}
.card-container {
  width: 95%;
  display: flex;
  align-items: center;
  overflow-x: hidden;
  height: 88vh;
  padding-left: 70px;

}

.arrow-left{
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 24px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.arrow-right {
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 24px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}

.arrow-left {
  left: 410px;
}
.arrow-left:hover{
  background-color: #7B1FA2;
  padding: 10px;
  border-radius: 100px;
}

.arrow-right {
  right: 430px;
}

.arrow-right:hover{
  background-color: #7B1FA2;
  padding: 10px;
  border-radius: 100px;
}


.disabled {
  pointer-events: none;
}



